import React, { useState, useEffect } from 'react';
import {
  Typography,
  Grid,
  Paper,
  Box,
  Card,
  CardContent,
  LinearProgress,
  Chip,
  Avatar,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Speed as SpeedIcon,
  Memory as MemoryIcon,
  Storage as StorageIcon,
  CloudQueue as CloudIcon,
  TrendingUp as TrendingUpIcon,
  CheckCircle as CheckCircleIcon,
  Warning as WarningIcon,
  Refresh as RefreshIcon,
} from '@mui/icons-material';
import axios from 'axios';

interface SystemStats {
  cpu_usage: number;
  memory_usage: number;
  disk_usage: number;
  active_connections: number;
  requests_per_minute: number;
  cache_hit_rate: number;
}

const Dashboard: React.FC = () => {
  const [stats, setStats] = useState<SystemStats>({
    cpu_usage: 45,
    memory_usage: 62,
    disk_usage: 38,
    active_connections: 12,
    requests_per_minute: 145,
    cache_hit_rate: 85,
  });
  const [loading, setLoading] = useState(false);

  const fetchStats = async () => {
    setLoading(true);
    try {
      // In production, fetch real stats from backend
      await new Promise(resolve => setTimeout(resolve, 500));
      // Mock data for now
      setStats({
        cpu_usage: Math.random() * 100,
        memory_usage: Math.random() * 100,
        disk_usage: Math.random() * 100,
        active_connections: Math.floor(Math.random() * 50),
        requests_per_minute: Math.floor(Math.random() * 500),
        cache_hit_rate: 75 + Math.random() * 25,
      });
    } catch (error) {
      console.error('Error fetching stats:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchStats();
    const interval = setInterval(fetchStats, 30000); // Refresh every 30 seconds
    return () => clearInterval(interval);
  }, []);

  const StatCard = ({ title, value, icon, color, unit = '', trend }: any) => (
    <Card
      className="card-hover"
      sx={{
        background: 'linear-gradient(135deg, rgba(102, 126, 234, 0.05) 0%, rgba(118, 75, 162, 0.05) 100%)',
        border: '1px solid rgba(102, 126, 234, 0.1)',
        height: '100%',
      }}
    >
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
          <Box>
            <Typography variant="body2" color="text.secondary" gutterBottom>
              {title}
            </Typography>
            <Typography variant="h4" sx={{ fontWeight: 700, color }}>
              {typeof value === 'number' ? value.toFixed(1) : value}{unit}
            </Typography>
          </Box>
          <Avatar
            sx={{
              background: `linear-gradient(135deg, ${color}20 0%, ${color}40 100%)`,
              color,
              width: 56,
              height: 56,
            }}
          >
            {icon}
          </Avatar>
        </Box>
        {typeof value === 'number' && unit === '%' && (
          <LinearProgress
            variant="determinate"
            value={value}
            sx={{
              height: 8,
              borderRadius: 4,
              backgroundColor: 'rgba(102, 126, 234, 0.1)',
              '& .MuiLinearProgress-bar': {
                background: `linear-gradient(90deg, ${color} 0%, ${color}dd 100%)`,
                borderRadius: 4,
              },
            }}
          />
        )}
        {trend && (
          <Box sx={{ display: 'flex', alignItems: 'center', mt: 1, gap: 0.5 }}>
            <TrendingUpIcon sx={{ fontSize: 16, color: '#10b981' }} />
            <Typography variant="caption" sx={{ color: '#10b981', fontWeight: 600 }}>
              {trend}
            </Typography>
          </Box>
        )}
      </CardContent>
    </Card>
  );

  const ServiceStatus = ({ name, status, latency }: any) => (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        p: 2,
        borderRadius: 2,
        background: 'rgba(102, 126, 234, 0.05)',
        border: '1px solid rgba(102, 126, 234, 0.1)',
        transition: 'all 0.25s ease-in-out',
        '&:hover': {
          background: 'rgba(102, 126, 234, 0.1)',
          transform: 'translateX(4px)',
        },
      }}
    >
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
        <Box
          sx={{
            width: 12,
            height: 12,
            borderRadius: '50%',
            background: status === 'online' ? '#10b981' : '#ef4444',
            boxShadow: `0 0 12px ${status === 'online' ? '#10b981' : '#ef4444'}`,
            animation: 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite',
          }}
        />
        <Typography variant="body1" fontWeight={600}>
          {name}
        </Typography>
      </Box>
      <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
        <Chip
          label={`${latency}ms`}
          size="small"
          sx={{
            background: 'rgba(102, 126, 234, 0.2)',
            color: '#667eea',
            fontWeight: 600,
          }}
        />
        <Chip
          icon={status === 'online' ? <CheckCircleIcon /> : <WarningIcon />}
          label={status === 'online' ? 'Online' : 'Offline'}
          size="small"
          color={status === 'online' ? 'success' : 'error'}
          sx={{ fontWeight: 600 }}
        />
      </Box>
    </Box>
  );

  return (
    <Box className="fade-in">
      {/* Header */}
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Box>
          <Typography
            variant="h3"
            sx={{
              fontWeight: 700,
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              WebkitBackgroundClip: 'text',
              WebkitTextFillColor: 'transparent',
              backgroundClip: 'text',
              mb: 1,
            }}
          >
            Dashboard
          </Typography>
          <Typography variant="body1" color="text.secondary">
            Welcome to ZainOne Orchestrator Studio
          </Typography>
        </Box>
        <Tooltip title="Refresh Stats">
          <IconButton
            onClick={fetchStats}
            disabled={loading}
            sx={{
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              color: 'white',
              '&:hover': {
                background: 'linear-gradient(135deg, #764ba2 0%, #667eea 100%)',
                transform: 'rotate(180deg)',
              },
              transition: 'all 0.5s ease-in-out',
            }}
          >
            <RefreshIcon />
          </IconButton>
        </Tooltip>
      </Box>

      {/* Stats Grid */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="CPU Usage"
            value={stats.cpu_usage}
            unit="%"
            icon={<SpeedIcon />}
            color="#667eea"
            trend="+2.5% from last hour"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Memory Usage"
            value={stats.memory_usage}
            unit="%"
            icon={<MemoryIcon />}
            color="#764ba2"
            trend="+1.2% from last hour"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Disk Usage"
            value={stats.disk_usage}
            unit="%"
            icon={<StorageIcon />}
            color="#10b981"
            trend="-0.5% from last hour"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Cache Hit Rate"
            value={stats.cache_hit_rate}
            unit="%"
            icon={<CloudIcon />}
            color="#3b82f6"
            trend="+3.1% from last hour"
          />
        </Grid>
      </Grid>

      {/* Additional Stats */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%' }}>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mb: 3 }}>
              System Metrics
            </Typography>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <Box sx={{ textAlign: 'center', p: 2, borderRadius: 2, background: 'rgba(102, 126, 234, 0.05)' }}>
                  <Typography variant="h4" sx={{ fontWeight: 700, color: '#667eea', mb: 1 }}>
                    {stats.active_connections}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Active Connections
                  </Typography>
                </Box>
              </Grid>
              <Grid item xs={6}>
                <Box sx={{ textAlign: 'center', p: 2, borderRadius: 2, background: 'rgba(118, 75, 162, 0.05)' }}>
                  <Typography variant="h4" sx={{ fontWeight: 700, color: '#764ba2', mb: 1 }}>
                    {stats.requests_per_minute}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    Requests/Min
                  </Typography>
                </Box>
              </Grid>
            </Grid>
          </Paper>
        </Grid>

        <Grid item xs={12} md={6}>
          <Paper sx={{ p: 3, height: '100%' }}>
            <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mb: 3 }}>
              Service Status
            </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              <ServiceStatus name="LLM Service" status="online" latency={45} />
              <ServiceStatus name="Vector Database" status="online" latency={12} />
              <ServiceStatus name="Redis Cache" status="online" latency={8} />
            </Box>
          </Paper>
        </Grid>
      </Grid>

      {/* Recent Activity */}
      <Paper sx={{ p: 3 }}>
        <Typography variant="h6" gutterBottom sx={{ fontWeight: 600, mb: 3 }}>
          Recent Activity
        </Typography>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {([
            { time: '2 minutes ago', action: 'LLM configuration updated', user: 'Admin' },
            { time: '15 minutes ago', action: 'New tool registered: web_search', user: 'System' },
            { time: '1 hour ago', action: 'Cache cleared successfully', user: 'Admin' },
            { time: '2 hours ago', action: 'Database backup completed', user: 'System' },
          ] as Array<{ time: string; action: string; user: string }>).map((activity, index) => (
            <Box
              key={index}
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                p: 2,
                borderRadius: 2,
                background: 'rgba(102, 126, 234, 0.05)',
                border: '1px solid rgba(102, 126, 234, 0.1)',
                transition: 'all 0.25s ease-in-out',
                '&:hover': {
                  background: 'rgba(102, 126, 234, 0.1)',
                  transform: 'translateX(4px)',
                },
              }}
            >
              <Box>
                <Typography variant="body1" fontWeight={600}>
                  {activity.action}
                </Typography>
                <Typography variant="caption" color="text.secondary">
                  {activity.time}  by {activity.user}
                </Typography>
              </Box>
              <CheckCircleIcon sx={{ color: '#10b981' }} />
            </Box>
          ))}
        </Box>
      </Paper>
    </Box>
  );
};

export default Dashboard;
