import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import {
  Drawer,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Typography,
  Divider,
  Box,
  Avatar,
  useTheme,
} from '@mui/material';
import DashboardIcon from '@mui/icons-material/Dashboard';
import FolderIcon from '@mui/icons-material/Folder';
import ChatIcon from '@mui/icons-material/Chat';
import SettingsIcon from '@mui/icons-material/Settings';
import StorageIcon from '@mui/icons-material/Storage';
import UpgradeIcon from '@mui/icons-material/Upgrade';
import MemoryIcon from '@mui/icons-material/Memory';
import BuildIcon from '@mui/icons-material/Build';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import MonitorHeartIcon from '@mui/icons-material/MonitorHeart';
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';

const drawerWidth = 280;

interface MenuItem {
  text: string;
  path: string;
  icon: React.ReactElement;
  badge?: string;
}

const menuItems: MenuItem[] = [
  { text: 'Dashboard', path: '/', icon: <DashboardIcon /> },
  { text: 'File Explorer', path: '/files', icon: <FolderIcon /> },
  { text: 'Chat Studio', path: '/chat', icon: <ChatIcon /> },
  { text: 'Topology', path: '/topology', icon: <AccountTreeIcon /> },
  { text: 'Monitoring', path: '/monitoring', icon: <MonitorHeartIcon /> },
  { text: 'LLM Config', path: '/llm', icon: <SettingsIcon /> },
  { text: 'Tools Config', path: '/tools', icon: <BuildIcon /> },
  { text: 'Memory & Cache', path: '/memory', icon: <MemoryIcon /> },
  { text: 'DB Management', path: '/db', icon: <StorageIcon /> },
  { text: 'Upgrades', path: '/upgrades', icon: <UpgradeIcon /> },
];

const adminMenuItem: MenuItem = {
  text: 'Admin Panel',
  path: '/admin',
  icon: <AdminPanelSettingsIcon />,
  badge: 'Admin',
};

const Sidebar: React.FC = () => {
  const location = useLocation();
  const theme = useTheme();
  const isDark = theme.palette.mode === 'dark';

  return (
    <Drawer
      variant="permanent"
      sx={{
        width: drawerWidth,
        flexShrink: 0,
        '& .MuiDrawer-paper': {
          width: drawerWidth,
          boxSizing: 'border-box',
          background: isDark
            ? 'linear-gradient(180deg, #1a1a2e 0%, #16213e 100%)'
            : 'linear-gradient(180deg, #ffffff 0%, #f8fafc 100%)',
          borderRight: isDark
            ? '1px solid rgba(102, 126, 234, 0.1)'
            : '1px solid rgba(0, 0, 0, 0.08)',
          boxShadow: isDark
            ? '4px 0 24px rgba(0, 0, 0, 0.3)'
            : '4px 0 24px rgba(0, 0, 0, 0.08)',
        },
      }}
    >
      {/* Logo Section */}
      <Box
        sx={{
          p: 3,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          background: isDark
            ? 'linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%)'
            : 'linear-gradient(135deg, rgba(90, 103, 216, 0.08) 0%, rgba(107, 70, 193, 0.08) 100%)',
          borderBottom: isDark
            ? '1px solid rgba(102, 126, 234, 0.2)'
            : '1px solid rgba(0, 0, 0, 0.08)',
        }}
      >
        <Box
          component="img"
          src="/company-logo.png"
          alt="AI Orchestrator Studio Logo"
          sx={{
            width: '180px',
            height: 'auto',
            maxHeight: '60px',
            mb: 2,
            objectFit: 'contain',
            filter: isDark ? 'brightness(1.1)' : 'brightness(1)',
            transition: 'all 0.3s ease-in-out',
            '&:hover': {
              transform: 'scale(1.05)',
              filter: isDark ? 'brightness(1.2) drop-shadow(0 0 20px rgba(102, 126, 234, 0.6))' : 'brightness(1.1) drop-shadow(0 0 20px rgba(90, 103, 216, 0.5))',
            },
          }}
        />
        <Typography
          variant="h5"
          sx={{
            fontWeight: 700,
            background: isDark
              ? 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)'
              : 'linear-gradient(135deg, #5a67d8 0%, #6b46c1 100%)',
            WebkitBackgroundClip: 'text',
            WebkitTextFillColor: 'transparent',
            backgroundClip: 'text',
            textAlign: 'center',
            mb: 0.5,
            fontSize: '1.3rem',
          }}
        >
          AI Orchestrator Studio
        </Typography>
        <Typography
          variant="caption"
          sx={{
            color: 'text.secondary',
            textAlign: 'center',
            fontWeight: 500,
            letterSpacing: '0.5px',
          }}
        >
          Intelligent Workflow Management
        </Typography>
      </Box>

      <Divider sx={{ borderColor: isDark ? 'rgba(102, 126, 234, 0.1)' : 'rgba(0, 0, 0, 0.08)' }} />

      {/* Main Menu */}
      <List sx={{ px: 1, py: 2 }}>
        {menuItems.map((item) => (
          <ListItem key={item.text} disablePadding sx={{ mb: 0.5 }}>
            <ListItemButton
              component={Link}
              to={item.path}
              selected={location.pathname === item.path}
              sx={{
                borderRadius: 2,
                mx: 1,
                transition: 'all 0.25s ease-in-out',
                '&:hover': {
                  backgroundColor: isDark
                    ? 'rgba(102, 126, 234, 0.15)'
                    : 'rgba(90, 103, 216, 0.1)',
                  transform: 'translateX(4px)',
                  '& .MuiListItemIcon-root': {
                    color: isDark ? '#667eea' : '#5a67d8',
                    transform: 'scale(1.1)',
                  },
                },
                '&.Mui-selected': {
                  backgroundColor: isDark
                    ? 'rgba(102, 126, 234, 0.2)'
                    : 'rgba(90, 103, 216, 0.15)',
                  borderLeft: isDark ? '3px solid #667eea' : '3px solid #5a67d8',
                  '&:hover': {
                    backgroundColor: isDark
                      ? 'rgba(102, 126, 234, 0.25)'
                      : 'rgba(90, 103, 216, 0.2)',
                  },
                  '& .MuiListItemIcon-root': {
                    color: isDark ? '#667eea' : '#5a67d8',
                  },
                  '& .MuiListItemText-primary': {
                    color: isDark ? '#667eea' : '#5a67d8',
                    fontWeight: 600,
                  },
                },
              }}
            >
              <ListItemIcon
                sx={{
                  color: location.pathname === item.path
                    ? (isDark ? '#667eea' : '#5a67d8')
                    : 'text.secondary',
                  minWidth: 40,
                  transition: 'all 0.25s ease-in-out',
                }}
              >
                {item.icon}
              </ListItemIcon>
              <ListItemText
                primary={item.text}
                primaryTypographyProps={{
                  fontSize: '0.95rem',
                  fontWeight: location.pathname === item.path ? 600 : 500,
                }}
              />
            </ListItemButton>
          </ListItem>
        ))}
      </List>

      <Divider sx={{ borderColor: isDark ? 'rgba(102, 126, 234, 0.1)' : 'rgba(0, 0, 0, 0.08)', my: 1 }} />

      {/* Admin Section */}
      <List sx={{ px: 1, py: 1 }}>
        <ListItem disablePadding>
          <ListItemButton
            component={Link}
            to={adminMenuItem.path}
            selected={location.pathname === adminMenuItem.path}
            sx={{
              borderRadius: 2,
              mx: 1,
              background: location.pathname === adminMenuItem.path
                ? (isDark
                  ? 'linear-gradient(135deg, rgba(239, 68, 68, 0.2) 0%, rgba(220, 38, 38, 0.2) 100%)'
                  : 'linear-gradient(135deg, rgba(220, 38, 38, 0.15) 0%, rgba(185, 28, 28, 0.15) 100%)')
                : (isDark ? 'rgba(239, 68, 68, 0.05)' : 'rgba(220, 38, 38, 0.05)'),
              border: isDark
                ? '1px solid rgba(239, 68, 68, 0.2)'
                : '1px solid rgba(220, 38, 38, 0.2)',
              transition: 'all 0.25s ease-in-out',
              '&:hover': {
                background: isDark
                  ? 'linear-gradient(135deg, rgba(239, 68, 68, 0.25) 0%, rgba(220, 38, 38, 0.25) 100%)'
                  : 'linear-gradient(135deg, rgba(220, 38, 38, 0.2) 0%, rgba(185, 28, 28, 0.2) 100%)',
                transform: 'translateX(4px)',
                borderColor: isDark ? 'rgba(239, 68, 68, 0.4)' : 'rgba(220, 38, 38, 0.4)',
                '& .MuiListItemIcon-root': {
                  color: isDark ? '#ef4444' : '#dc2626',
                  transform: 'scale(1.1) rotate(10deg)',
                },
              },
              '&.Mui-selected': {
                background: isDark
                  ? 'linear-gradient(135deg, rgba(239, 68, 68, 0.3) 0%, rgba(220, 38, 38, 0.3) 100%)'
                  : 'linear-gradient(135deg, rgba(220, 38, 38, 0.25) 0%, rgba(185, 28, 28, 0.25) 100%)',
                borderLeft: isDark ? '3px solid #ef4444' : '3px solid #dc2626',
                '& .MuiListItemIcon-root': {
                  color: isDark ? '#ef4444' : '#dc2626',
                },
                '& .MuiListItemText-primary': {
                  color: isDark ? '#ef4444' : '#dc2626',
                  fontWeight: 600,
                },
              },
            }}
          >
            <ListItemIcon
              sx={{
                color: location.pathname === adminMenuItem.path
                  ? (isDark ? '#ef4444' : '#dc2626')
                  : (isDark ? '#f87171' : '#ef4444'),
                minWidth: 40,
                transition: 'all 0.25s ease-in-out',
              }}
            >
              {adminMenuItem.icon}
            </ListItemIcon>
            <ListItemText
              primary={adminMenuItem.text}
              primaryTypographyProps={{
                fontSize: '0.95rem',
                fontWeight: location.pathname === adminMenuItem.path ? 600 : 500,
              }}
            />
            {adminMenuItem.badge && (
              <Box
                sx={{
                  px: 1,
                  py: 0.5,
                  borderRadius: 1,
                  background: isDark
                    ? 'linear-gradient(135deg, #ef4444 0%, #dc2626 100%)'
                    : 'linear-gradient(135deg, #dc2626 0%, #b91c1c 100%)',
                  fontSize: '0.7rem',
                  fontWeight: 700,
                  color: 'white',
                  boxShadow: isDark
                    ? '0 0 10px rgba(239, 68, 68, 0.4)'
                    : '0 0 10px rgba(220, 38, 38, 0.4)',
                }}
              >
                {adminMenuItem.badge}
              </Box>
            )}
          </ListItemButton>
        </ListItem>
      </List>

      {/* Footer */}
      <Box
        sx={{
          mt: 'auto',
          p: 2,
          borderTop: isDark
            ? '1px solid rgba(102, 126, 234, 0.1)'
            : '1px solid rgba(0, 0, 0, 0.08)',
          background: isDark
            ? 'rgba(102, 126, 234, 0.05)'
            : 'rgba(90, 103, 216, 0.05)',
        }}
      >
        <Typography
          variant="caption"
          sx={{
            display: 'block',
            textAlign: 'center',
            color: 'text.secondary',
            mb: 0.5,
          }}
        >
          Version 1.0.0
        </Typography>
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            gap: 0.5,
          }}
        >
          <Box
            sx={{
              width: 8,
              height: 8,
              borderRadius: '50%',
              background: '#10b981',
              boxShadow: '0 0 8px #10b981',
              animation: 'pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite',
              '@keyframes pulse': {
                '0%, 100%': { opacity: 1 },
                '50%': { opacity: 0.5 },
              },
            }}
          />
          <Typography variant="caption" sx={{ color: '#10b981', fontWeight: 600 }}>
            System Online
          </Typography>
        </Box>
      </Box>
    </Drawer>
  );
};

export default Sidebar;
